	module.exports = {"data":{"postsJson":{"title":"더 많은 능력과 가르침","questionsTitle":"명령과 약속","display":"block","bookName":"누가복음","ref":"17:11-19:27","html":"<p>   <strong>17</strong><u>11</u>예수께서 예루살렘으로 가실 때에 사마리아와 갈릴리 사이로 지나가시다가 <u>12</u>한 마을에 들어가시니 나병환자 열 명이 예수를 만나 멀리 서서 <u>13</u>소리를 높여 이르되</p><h1>열명의 나병환자</h1><h4>예수 선생님이여 우리를 불쌍히 여기소서</h4><p>하거늘 <u>14</u>보시고 이르시되</p><h1>예수님</h1><h2>가서 제사장들에게 너희 몸을 보이라</h2><p>하셨더니 그들이 가다가 깨끗함을 받은지라<br />   <u>15</u>그 중의 한 사람이 자기가 나은 것을 보고 큰 소리로 하나님께 영광을 돌리며 돌아와 <u>16</u>예수의 발 아래에 엎드리어 감사하니 그는 사마리아 사람이라</p><p><u>17</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>열 사람이 다 깨끗함을 받지 아니하였느냐 그 아홉은 어디 있느냐 <u>18</u>이 이방인 외에는 하나님께 영광을 돌리러 돌아온 자가 없느냐</h2><p>하시고 <u>19</u>그에게 이르시되</p><h1>예수님</h1><h2>일어나 가라 네 믿음이 너를 구원하였느니라</h2><p>하시더라<br />   <u>20</u>바리새인들이</p><h1>바리새인들</h1><h4>하나님의 나라가 어느 때에 임하나이까</h4><p>묻거늘 예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>하나님의 나라는 볼 수 있게 임하는 것이 아니요 <u>21</u>또 여기 있다 저기 있다고도 못하리니 하나님의 나라는 너희 안에 있느니라</h2><p><u>22</u>또 제자들에게 이르시되</p><h1>예수님</h1><h2>때가 이르리니 너희가 인자의 날 하루를 보고자 하되 보지 못하리라 <u>23</u>사람이 너희에게 말하되 보라 저기 있다 보라 여기 있다 하리라 그러나 너희는 가지도 말고 따르지도 말라 <u>24</u>번개가 하늘 아래 이쪽에서 번쩍이어 하늘 아래 저쪽까지 비침같이 인자도 자기 날에 그러하리라 <u>25</u>그러나 그가 먼저 많은 고난을 받으며 이 세대에게 버린 바 되어야 할지니라<br />   <u>26</u>노아의 때에 된 것과 같이 인자의 때에도 그러하리라 <u>27</u>노아가 방주에 들어가던 날까지 사람들이 먹고 마시고 장가 들고 시집 가더니 홍수가 나서 그들을 다 멸망시켰으며<br />   <u>28</u>또 롯의 때와 같으리니 사람들이 먹고 마시고 사고 팔고 심고 집을 짓더니 <u>29</u>롯이 소돔에서 나가던 날에 하늘로부터 불과 유황이 비오듯 하여 그들을 멸망시켰느니라 <u>30</u>인자가 나타나는 날에도 이러하리라 <u>31</u>그 날에 만일 사람이 지붕 위에 있고 그의 세간이 그 집 안에 있으면 그것을 가지러 내려가지 말 것이요 밭에 있는 자도 그와 같이 뒤로 돌이키지 말 것이니라 <u>32</u>롯의 처를 기억하라 <u>33</u>무릇 자기 목숨을 보전하고자 하는 자는 잃을 것이요 잃는 자는 살리리라 <u>34</u>내가 너희에게 이르노니 그 밤에 둘이 한 자리에 누워 있으매 하나는 데려감을 얻고 하나는 버려둠을 당할 것이요 <u>35</u>두 여자가 함께 맷돌을 갈고 있으매 하나는 데려감을 얻고 하나는 버려둠을 당할 것이니라 <u>36</u>(없음)</h2><p><u>37</u>그들이 대답하여 이르되</p><h1>제자들</h1><h3>주여어디오니이까</h3><p>이르시되</p><h1>예수님</h1><h2>주검 있는 곳에는 독수리가 모이느니라</h2><p>하시니라<br />   <strong>18</strong><u>1</u>예수께서 그들에게 항상 기도하고 낙심하지 말아야 할 것을 비유로 말씀하여 <u>2</u>이르시되</p><h1>예수님</h1><h2>어떤 도시에 하나님을 두려워하지 않고 사람을 무시하는 한 재판장이 있는데 <u>3</u>그 도시에 한 과부가 있어 자주 그에게 가서 내 원수에 대한 나의 원한을 풀어 주소서 하되 <u>4</u>그가 얼마 동안 듣지 아니하다가 후에 속으로 생각하되 내가 하나님을 두려워하지 않고 사람을 무시하나 <u>5</u>이 과부가 나를 번거롭게 하니 내가 그 원한을 풀어 주리라 그렇지 않으면 늘 와서 나를 괴롭게 하리라 하였느니라</h2><p><u>6</u>주께서 또 이르시되</p><h1>예수님</h1><h2>불의한 재판장이 말한 것을 들으라 <u>7</u>하물며 하나님께서 그 밤낮 부르짖는 택하신 자들의 원한을 풀어 주지 아니하시겠느냐 그들에게 오래 참으시겠느냐 <u>8</u>내가 너희에게 이르노니 속히 그 원한을 풀어 주시리라 그러나 인자가 올 때에 세상에서 믿음을 보겠느냐</h2><p>하시니라<br />   <u>9</u>또 자기를 의롭다고 믿고 다른 사람을 멸시하는 자들에게 이 비유로 말씀하시되</p><h1>예수님</h1><h2><u>10</u>두 사람이 기도하러 성전에 올라가니 하나는 바리새인이요 하나는 세리라 <u>11</u>바리새인은 서서 따로 기도하여 이르되 하나님이여 나는 다른 사람들 곧 토색, 불의, 간음을 하는 자들과 같지 아니하고 이 세리와도 같지 아니함을 감사하나이다 <u>12</u>나는 이레에 두 번씩 금식하고 또 소득의 십일조를 드리나이다 하고 <u>13</u>세리는 멀리 서서 감히 눈을 들어 하늘을 쳐다보지도 못하고 다만 가슴을 치며 이르되 하나님이여 불쌍히 여기소서 나는 죄인이로소이다 하였느니라<br />   <u>14</u>내가 너희에게 이르노니 이에 저 바리새인이 아니고 이 사람이 의롭다 하심을 받고 그의 집으로 내려갔느니라 무릇 자기를 높이는 자는 낮아지고 자기를 낮추는 자는 높아지리라</h2><p>하시니라<br />   <u>15</u>사람들이 예수께서 만져 주심을 바라고 자기 어린 아기를 데리고 오매 제자들이 보고 꾸짖거늘 <u>16</u>예수께서 그 어린 아이들을 불러 가까이 하시고 이르시되</p><h1>예수님</h1><h2>어린 아이들이 내게 오는 것을 용납하고 금하지 말라 하나님의 나라가 이런 자의 것이니라 <u>17</u>내가 진실로 너희에게 이르노니 누구든지 하나님의 나라를 어린 아이와 같이 받아들이지 않는 자는 결단코 거기 들어가지 못하리라</h2><p>하시니라<br />   <u>18</u>어떤 관리가 물어 이르되</p><h1>부자 청년</h1><h4>선한 선생님이여 내가 무엇을 하여야 영생을 얻으리이까</h4><p><u>19</u>예수께서 이르시되</p><h1>예수님</h1><h2>네가 어찌하여 나를 선하다 일컫느냐 하나님 한 분 외에는 선한 이가 없느니라 <u>20</u>네가 계명을 아나니 간음하지 말라, 살인하지 말라, 도둑질하지 말라, 거짓 증언 하지 말라, 네 부모를 공경하라</h2><p>하였느니라<br />   <u>21</u>여짜오되</p><h1>부자 청년</h1><h4>이것은 내가 어려서부터 다 지키었나이다</h4><p><u>22</u>예수께서 이 말을 들으시고 이르시되</p><h1>예수님</h1><h2>네게 아직도 한 가지 부족한 것이 있으니 네게 있는 것을 다 팔아 가난한 자들에게 나눠 주라 그리하면 하늘에서 네게 보화가 있으리라 그리고 와서 나를 따르라</h2><p>하시니 <u>23</u>그 사람이 큰 부자이므로 이 말씀을 듣고 심히 근심하더라<br />   <u>24</u>예수께서 그를 보시고 이르시되</p><h1>예수님</h1><h2>재물이 있는 자는 하나님의 나라에 들어가기가 얼마나 어려운지 <u>25</u>낙타가 바늘귀로 들어가는 것이 부자가 하나님의 나라에 들어가는 것보다 쉬우니라</h2><p>하시니 <u>26</u>듣는 자들이 이르되</p><h1>군중들</h1><h4>그런즉 누가 구원을 얻을 수 있나이까</h4><p><u>27</u>이르시되</p><h1>예수님</h1><h2>무릇 사람이 할 수 없는 것을 하나님은 하실 수 있느니라</h2><p><u>28</u>베드로가 여짜오되</p><h1>시몬 베드로</h1><h3>보옵소서우리가우리의것을다버리고주를따랐나이다</h3><p><u>29</u>이르시되</p><h1>예수님</h1><h2>내가 진실로 너희에게 이르노니 하나님의 나라를 위하여 집이나 아내나 형제나 부모나 자녀를 버린 자는 <u>30</u>현세에 여러 배를 받고 내세에 영생을 받지 못할 자가 없느니라</h2><p>하시니라<br />   <u>31</u>예수께서 열두 제자를 데리시고 이르시되</p><h1>예수님</h1><h2>보라 우리가 예루살렘으로 올라가노니 선지자들을 통하여 기록된 모든 것이 인자에게 응하리라 <u>32</u>인자가 이방인들에게 넘겨져 희롱을 당하고 능욕을 당하고 침 뱉음을 당하겠으며 <u>33</u>그들은 채찍질하고 그를 죽일 것이나 그는 삼 일 만에 살아나리라</h2><p>하시되 <u>34</u>제자들이 이것을 하나도 깨닫지 못하였으니 그 말씀이 감취었으므로 그들이 그 이르신 바를 알지 못하였더라<br />   <u>35</u>여리고에 가까이 가셨을 때에 한 맹인이 길 가에 앉아 구걸하다가 <u>36</u>무리가 지나감을 듣고</p><h1>바디매오</h1><h4>이 무슨 일이냐</h4><p>고 물은대 <u>37</u>그들이 나사렛 예수께서 지나가신다 하니<br />   <u>38</u>맹인이 외쳐 이르되</p><h1>바디매오</h1><h4>다윗의 자손 예수여 나를 불쌍히 여기소서</h4><p>하거늘 <u>39</u>앞서 가는 자들이 그를 꾸짖어</p><h1>군중들</h1><h4>잠잠하라</h4><p>하되 그가 더욱 크게 소리 질러</p><h1>바디매오</h1><h4>다윗의 자손이여 나를 불쌍히 여기소서</h4><p>하는지라<br />   <u>40</u>예수께서 머물러 서서 명하여</p><h1>예수님</h1><h2>데려오라</h2><p>하셨더니 그가 가까이 오매 물어 이르시되</p><h1>예수님</h1><h2>41네게 무엇을 하여 주기를 원하느냐</h2><p>이르되</p><h1>바디매오</h1><h4>주여 보기를 원하나이다</h4><p><u>42</u>예수께서 그에게 이르시되</p><h1>예수님</h1><h2>보라 네 믿음이 너를 구원하였느니라</h2><p>하시매 <u>43</u>곧 보게 되어 하나님께 영광을 돌리며 예수를 따르니 백성이 다 이를 보고 하나님을 찬양하니라<br />   <strong>19</strong><u>1</u>예수께서 여리고로 들어가 지나가시더라 <u>2</u>삭개오라 이름하는 자가 있으니 세리장이요 또한 부자라<u>3</u>그가 예수께서 어떠한 사람인가 하여 보고자 하되 키가 작고 사람이 많아 할 수 없어 <u>4</u>앞으로 달려가서 보기 위하여 돌무화과나무에 올라가니 이는 예수께서 그리로 지나가시게 됨이러라<br />   <u>5</u>예수께서 그 곳에 이르사 쳐다 보시고 이르시되</p><h1>예수님</h1><h2>삭개오야 속히 내려오라 내가 오늘 네 집에 유하여야 하겠다</h2><p>하시니 <u>6</u>급히 내려와 즐거워하며 영접하거늘 <u>7</u>뭇 사람이 보고 수군거려 이르되</p><h1>군중들</h1><h4>저가 죄인의 집에 유하러 들어갔도다</h4><p>하더라<br />   <u>8</u>삭개오가 서서 주께 여짜오되</p><h1>삭개오</h1><h4>주여 보시옵소서 내 소유의 절반을 가난한 자들에게 주겠사오며 만일 누구의 것을 속여 빼앗은 일이 있으면 네 갑절이나 갚겠나이다</h4><p><u>9</u>예수께서 이르시되</p><h1>예수님</h1><h2>오늘 구원이 이 집에 이르렀으니 이 사람도 아브라함의 자손임이로다 <u>10</u>인자가 온 것은 잃어버린 자를 찾아 구원하려 함이니라</h2><p><u>11</u>그들이 이 말씀을 듣고 있을 때에 비유를 더하여 말씀하시니 이는 자기가 예루살렘에 가까이 오셨고 그들은 하나님의 나라가 당장에 나타날 줄로 생각함이더라 <u>12</u>이르시되</p><h1>예수님</h1><h2>어떤 귀인이 왕위를 받아가지고 오려고 먼 나라로 갈 때에 <u>13</u>그 종 열을 불러 은화 열 므나를 주며 이르되 내가 돌아올 때까지 장사하라 하니라 <u>14</u>그런데 그 백성이 그를 미워하여 사자를 뒤로 보내어 이르되 우리는 이 사람이 우리의 왕 됨을 원하지 아니하나이다 하였더라<br />   <u>15</u>귀인이 왕위를 받아가지고 돌아와서 은화를 준 종들이 각각 어떻게 장사하였는지를 알고자 하여 그들을 부르니 <u>16</u>그 첫째가 나아와 이르되 주인이여 당신의 한 므나로 열 므나를 남겼나이다<br />   <u>17</u>주인이 이르되 잘하였다 착한 종이여 네가 지극히 작은 것에 충성하였으니 열 고을 권세를 차지하라 하고 <u>18</u>그 둘째가 와서 이르되 주인이여 당신의 한 므나로 다섯 므나를 만들었나이다<br />   <u>19</u>주인이 그에게도 이르되 너도 다섯 고을을 차지하라 하고 <u>20</u>또 한 사람이 와서 이르되 주인이여 보소서 당신의 한 므나가 여기 있나이다 내가 수건으로 싸 두었었나이다 <u>21</u>이는 당신이 엄한 사람인 것을 내가 무서워함이라 당신은 두지 않은 것을 취하고 심지 않은 것을 거두나이다<br />   <u>22</u>주인이 이르되 악한 종아 내가 네 말로 너를 심판하노니 너는 내가 두지 않은 것을 취하고 심지 않은 것을 거두는 엄한 사람인 줄로 알았느냐 <u>23</u>그러면 어찌하여 내 돈을 은행에 맡기지 아니하였느냐 그리하였으면 내가 와서 그 이자와 함께 그 돈을 찾았으리라 하고 <u>24</u>곁에 섰는 자들에게 이르되 그 한 므나를 빼앗아 열 므나 있는 자에게 주라 하니 <u>25</u>그들이 이르되 주여 그에게 이미 열 므나가 있나이다<br />   <u>26</u>주인이 이르되 내가 너희에게 말하노니 무릇 있는 자는 받겠고 없는 자는 그 있는 것도 빼앗기리라 <u>27</u>그리고 내가 왕 됨을 원하지 아니하던 저 원수들을 이리로 끌어다가 내 앞에서 죽이라 하였느니라</h2>","path":"/335","Question1":"이 구절에서 하나님이 주신 지침들을 주목할 수 있습니까? ","Question2":"원래의 상황에 당신을 넣어 이런 지침을 받는 다는 것이 어떤 것이었을지 창의적으로 생각해보십시오. 오늘 이것이 당신과 일 어디에 관련되어있습니까?","Question3":"이 구절에서 어떤 하나님으로부터 영감받은 소망에 주목하십니까? 당신의 삶속에서 어떻게 이것을 더 가지도록 자랄 수 있습니까? ","Question4":"오늘 이 소망이 당신에게 어떤 의미인지 친구에게 말하십시오.","prevPath":"/334","nextPath":"/336"}},"pathContext":{}}

